*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  width: 100%;
}

h1, h2, h3 {
  line-height: 1;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  position: relative;
  line-height: 1.5;
  font-family: "Rubik", sans-serif;
  background: var(--vdark-blue);
}

.container {
  width: min(90%, 75rem);
  margin-inline: auto;
}

:root {
  --white: #fff;
  --black: #000;
  --prim-blue: hsl(246, 80%, 60%);
  --light-orange: hsl(15, 100%, 70%);
  --soft-blue: hsl(195, 74%, 62%);
  --light-red: hsl(348, 100%, 68%);
  --lime-green: hsl(145, 58%, 55%);
  --prim-violet: hsl(264, 64%, 52%);
  --soft-yellow: hsl(43, 84%, 65%);
  --vdark-blue: hsl(226, 43%, 10%);
  --dark-blue: hsl(235, 46%, 20%);
  --desat-blue: hsl(235, 45%, 61%);
  --pale-blue: hsl(236, 100%, 87%);
  --ff-rubik: "Rubik", sans-serif;
}

.main {
  display: grid;
  gap: 1.25rem;
}
.main .perfil {
  background: var(--dark-blue);
}